import React from "react";


export function Loading(props) {
    return (
        <div className="inner" style={{"width": "200px", "height": "200px", "background": "transparent"}} {...props}>
            <svg xmlns="http://www.w3.org/2000/svg"
                 style={{"margin": "auto", "background": "transparent", "display": "block"}}
                 width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <g transform="translate(20 50)">
                    <circle cx="0" cy="0" r="6" fill="#85a2b6">
                        <animateTransform attributeName="transform" type="scale" begin="-0.375s" calcMode="spline"
    keySplines="0.3 0 0.7 1;0.3 0 0.7 1" values="0;1;0" keyTimes="0;0.5;1" dur="1s"
    repeatCount="indefinite"/>
                    </circle>
                </g>
                <g transform="translate(40 50)">
                    <circle cx="0" cy="0" r="6" fill="#bbcedd">
                        <animateTransform attributeName="transform" type="scale" begin="-0.25s" calcMode="spline"
    keySplines="0.3 0 0.7 1;0.3 0 0.7 1" values="0;1;0" keyTimes="0;0.5;1" dur="1s"
    repeatCount="indefinite"/>
                    </circle>
                </g>
                <g transform="translate(60 50)">
                    <circle cx="0" cy="0" r="6" fill="#dce4eb">
                        <animateTransform attributeName="transform" type="scale" begin="-0.125s" calcMode="spline"
    keySplines="0.3 0 0.7 1;0.3 0 0.7 1" values="0;1;0" keyTimes="0;0.5;1" dur="1s"
    repeatCount="indefinite"/>
                    </circle>
                </g>
                <g transform="translate(80 50)">
                    <circle cx="0" cy="0" r="6" fill="#fdfdfd">
                        <animateTransform attributeName="transform" type="scale" begin="0s" calcMode="spline"
    keySplines="0.3 0 0.7 1;0.3 0 0.7 1" values="0;1;0" keyTimes="0;0.5;1" dur="1s"
    repeatCount="indefinite"/>
                    </circle>
                </g>
            </svg>
        </div>
    )
}


